import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';


import { registroPDL } from '../modelo_registroPDL/registroPDL';


//productos
import { productol } from '../modelo_producto/modelo_productol';

@Component({
  selector: 'app-registro-pedi-dia-ll',
  templateUrl: './registro-pedi-dia-ll.page.html',
  styleUrls: ['./registro-pedi-dia-ll.page.scss'],
})
export class RegistroPediDiaLlPage implements OnInit {

  objeto:registroPDL = { Des_de_alimnentos:"", Unidad_de_Media:"", Gr_ML_Desayuno:"", Gr_ML_Sopa:"", Gr_ML_Segundo:"", Gr_ML_Cena:""}

  pruebas: registroPDL[] = [];
  url = 'http://192.168.4.153/serve_api/crear_registroPDL.php';
  url2 = 'http://192.168.4.153/serve_api/menu_registroPDL.php';

  disabledButton;


    //producto

    objeto1:productol = { tipo:"", nombreali:"", cantidad_pro:"", cantidad_min:"", cantidad_med:"", cantidad_max:""}

    pruebas1: productol[] = [];
    url3 = 'http://192.168.4.153/serve_api/menu_productol.php';


  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders

  ) { 
    this.listarRegistroPDL();
    this.listarRegistroPA();
  }

  ngOnInit() {
  }

  agregarRegistroPDL(obj :registroPDL): Observable<registroPDL>{
    return this.http.post<registroPDL>(this.url, JSON.stringify(obj))
  }

  async agregarNU() {
    this.agregarRegistroPDL(this.objeto)
    .subscribe((resp) =>{
      this.objeto.Des_de_alimnentos='';
      this.objeto.Unidad_de_Media='';
      this.objeto.Gr_ML_Desayuno='';
      this.objeto.Gr_ML_Sopa='';
      this.objeto.Gr_ML_Segundo='';
      this.objeto.Gr_ML_Cena='';
      this.listarRegistroPDL();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }

  getRegistroPDL(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarRegistroPDL(){
    this.pruebas = [];
    this.getRegistroPDL().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas.push(res[i]);
      }
    })
  }

  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_PDL',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroPDL();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }


  abrirCrud(a){
    this.router.navigate(['/mod-pdl/'+a]);
  }


   //producto /////////////////

   getRegistroPA(): Observable<any>{
    return this.http.get(this.url3);
  }

  listarRegistroPA(){
    this.pruebas1 = [];
    this.getRegistroPA().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas1.push(res[i]);
      }
    })
  }







}
